export default {
	data() {
		return {
			swiperList: [],
			dotStyle: false,
			cardCur: 0,
			placeNo: '',
			list: [],
			CheckTime: '',
			CheckUser: '',
			PlaceName: '',
			endTime: '',
		}
	},
	methods: {
		ViewImage(e) {
			uni.previewImage({
				urls: e.currentTarget.dataset.all,
				current: e.currentTarget.dataset.url
			});
		},
		cardSwiper(e) {
			this.cardCur = e.detail.current
		},
		getDetail() {
			var self = this;
			uni.request({
				url: self.$store.state.urlPrefix + '/Web/Get_query_hdDetail',
				data:{
					placeNo: self.placeNo,
					CCType: '',
					YHXH: '',
					campusplaceNo: 'S000099100001',
					time: self.endTime,
					type: 'XF'
				}
			}).then(data => {
				var [error,res] = data;
				self.CheckTime = res.data.data.CheckTime;
				self.CheckUser = res.data.data.CheckUserName;
				self.PlaceName = res.data.data.PlaceName;
				self.list = res.data.data.Detail;
				for (let s of self.list) {
					s.ImgURL = s.ImgURL.split(',');
				}
				console.log(self.list)
				
				uni.hideLoading()
			})
		}
	},
	mounted() {
		this.getDetail()
	},
	onLoad(option) {
		uni.showLoading({
			title:"加载中。。。",
			mask:true
		})
		console.log(option)
		this.placeNo = option.placeNo
		this.endTime = option.time;
		if (this.endTime == '未结束') {
			this.endTime = '';
		}
	}
}
